    overflow-x: scroll;
    border-radius: 0.5rem;
  }
  
  .table {
    min-width: 900px; /* Increase minimum width for medium screens */
  }
}

@media (max-width: 768px) {
  .users-table {
    margin: 0 -1rem; /* Negative margin to allow full-width scrolling */
    border-radius: 0;
    border-left: none;
    border-right: none;
    overflow-x: scroll;
  }
  
  .table {
    min-width: 900px; /* Increase minimum width for mobile */
  }
  
  .table th,
  .table td {
    padding: 0.75rem 0.5rem; /* Reduce padding on mobile */
    font-size: 0.8rem; /* Smaller font on mobile */
  }
  
  .user-avatar-small {
    width: 28px;
    height: 28px;
  }
  
  .table-actions {
    gap: 0.25rem;
  }
  
  .edit-btn-small,
  .delete-btn-small {
    padding: 0.375rem;
  }
  
