    padding: 1rem;
  }
}

/* Success Modal Styles */
.success-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 1rem;
}

.success-modal {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  text-align: center;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: successModalSlideIn 0.3s ease-out;
}

@keyframes successModalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.success-icon {
  color: #158638;
  margin-bottom: 1rem;
}

.success-title {
  color: #158638;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 1rem 0;
}

.success-message {
  color: #6b7280;
  font-size: 1rem;
  margin: 0 0 1.5rem 0;
  line-height: 1.5;
}

.success-close-btn {
  background: #158638;
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 1rem;
}

.success-close-btn:hover {
  background: #16a34a;
  transform: translateY(-1px);
}

/* RTL Support for Success Modal */
[dir="rtl"] .success-modal {
  text-align: center;
}

/* Pagination Styles */
.pagination-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.pagination-info {
  text-align: center;
  color: #5F646D;
  font-size: 0.875rem;
  font-weight: 500;
}

.pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.page-size-control {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.page-size-control label {
  color: #374151;
  font-weight: 500;
  font-size: 0.875rem;
}

.page-size-control select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
}

.pagination {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 0.25rem;
}

.page-item {
  margin: 0;
}

.page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  height: 40px;
  padding: 0.5rem;
  border: 1px solid #555555;
  background: white;
  color: #5F646D;
  text-decoration: none;
  border-radius: 6px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.page-link:hover {
  background: #D4AA3A;
  border-color: #D4AA3A;
  color: #FFFFFF;
}

.page-item.active .page-link {
  background: #D4AA3A;
  border-color: #D4AA3A;
  color: #FFFFFF;
}

.page-item.disabled .page-link {
  background: #f8f9fa;
  border-color: #dee2e6;
  color: #6c757d;
  cursor: not-allowed;
}

/* Responsive Pagination */
@media (max-width: 480px) {
  .pagination-section {
    padding: 0.5rem;
  }
  .users-page {
    padding: 0.2rem;
  }
  .page-title{
    font-size: 1.8rem;
    margin-bottom: 1rem;
    text-align: center !important;
    justify-content: center;
    align-self: center;
    
  }
  .users-page.rtl {
    direction: rtl;
  }
  .user-card {
    width: 100% !important;
    max-width: 96% !important;
    margin: 0 auto !important;
    box-sizing: border-box !important;
  }
}
@media (max-width: 768px) {
  .pagination-controls {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
    text-align: center;
  }
  
  .page-size-control {
    justify-content: center;
    width: 100%;
    max-width: 200px;
  }
  
  .pagination {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .pagination-info {
    order: -1;
    margin-bottom: 1rem;
  }
}
